body {
  background-color: #f4f4f4;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  padding-right: 15px;
  padding-left: 15px;
}

header,
footer {
  background-color: #A10000;
  color: #fff;
}
header a,
footer a {
  color: #fff;
}
header ul,
header li,
footer ul,
footer li {
  list-style: none;
}

header .container {
  padding-right: 0;
  padding-left: 0;
}

footer {
  padding: 20px 0;
}
footer .copyright {
  text-align: center;
}

.friend-link {
  position: relative;
  padding-left: 100px;
}
.friend-link > div:first-child {
  position: absolute;
  left: 0;
  width: 90px;
  border-right: 5px solid #fff;
  text-align: center;
}
.friend-link a {
  margin-right: 20px;
}

.nav-bar li {
  line-height: 60px;
  height: 60px;
  display: inline-block;
  padding: 0 20px;
  position: relative;
}
.nav-bar li:hover {
  border-bottom: 3px solid #fff;
}
.nav-bar li:hover .sub-nav {
  display: block;
}
.nav-bar .search-icon {
  cursor: pointer;
}
.nav-bar .search-icon:hover {
  border-bottom: none;
}
.nav-bar ul {
  display: inline-block;
}
.nav-bar ul.nav-right {
  float: right;
}
.nav-bar .sub-nav {
  display: none;
  position: absolute;
  background-color: #fff;
  box-shadow: 0px 2px 8px 1px rgba(0, 0, 0, 0.2);
  left: 0;
  width: 100%;
  margin-top: -5px;
  z-index: 66;
}
.nav-bar .sub-nav a {
  color: #333;
  display: block;
}
.nav-bar .sub-nav a:hover {
  background-color: #f2f2f5;
  text-decoration: none;
  color: #eb7350;
}
.nav-bar .sub-nav ul {
  display: block;
}
.nav-bar .sub-nav li {
  display: block;
  padding: 0;
  text-align: center;
  height: 40px;
  line-height: 40px;
}
.nav-bar .nav-bar-toggle {
  display: none;
}
.nav-bar .new-tip {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: yellow;
  border-radius: 50%;
  position: absolute;
  top: 10px;
}

.metro-grid {
  margin: 50px 0;
  display: flex;
  align-items: baseline;
  justify-items: center;
  flex-wrap: wrap;
  justify-content: center;
}
.metro-grid a {
  display: block;
  line-height: 80px;
  height: 80px;
  width: 300px;
  font-size: 30px;
  text-align: center;
  background-color: #fff;
  margin: 8px;
  box-shadow: rgba(51, 51, 51, 0.7) 0 0 10px;
  position: relative;
  overflow: hidden;
}
.metro-grid a.unknow img {
  height: 20px;
}
.metro-grid a .desc {
  font-size: 20px;
  line-height: 40px;
  position: absolute;
  bottom: -40px;
  text-align: center;
  width: 100%;
  color: #af5d0d;
}
.metro-grid a .name,
.metro-grid a .desc {
  transition: all 1s;
}
.metro-grid a:hover .name {
  transform: scale(0.6) translateY(-30px);
}
.metro-grid a:hover .desc {
  bottom: 0;
}

.panel {
  background-color: #fff;
  margin-bottom: 20px;
}
.panel .panel-header {
  line-height: 50px;
  padding-left: 20px;
  border-left: 5px solid #A10000;
  font-size: 20px;
  font-weight: 700;
}
.panel .panel-header small {
  font-size: 12px;
  font-weight: 200;
  color: #767676;
}
.panel .panel-header .panel-menu {
  font-size: 14px;
  float: right;
  font-weight: 500;
  padding-right: 10px;
}
.panel .panel-header .panel-menu a {
  margin-left: 10px;
}
.panel .panel-body .metro-grid {
  margin: 0;
}
.panel .panel-body.scoll-box {
  max-height: 300px;
  overflow-y: auto;
}
.panel .panel-body.scoll-box::-webkit-scrollbar {
  height: 6px;
  width: 6px;
  margin-right: 5px;
  background: #f5f5f5;
  transition: all 0.3s ease-in-out;
  border-radius: 0px;
}
.panel .panel-body.scoll-box::-webkit-scrollbar-track {
  -webkit-border-radius: 0px;
  border-radius: 0px;
}
.panel .panel-body.scoll-box::-webkit-scrollbar-thumb {
  -webkit-border-radius: 0px;
  border-radius: 0px;
  background: rgba(0, 0, 0, 0.5);
}
.panel .panel-body.scoll-box::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.6);
}
.panel .panel-body.scoll-box::-webkit-scrollbar-thumb:active {
  background: rgba(0, 0, 0, 0.8);
}
.panel .panel-body.scoll-box::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(0, 0, 0, 0.4);
}
.panel .panel-body.scoll-box .more {
  text-align: center;
  line-height: 30px;
  display: block;
}
.panel .panel-body .note-item {
  border-bottom: rgba(161, 0, 0, 0.1) solid 1px;
  box-sizing: border-box;
  padding: 10px;
  counter-increment: item-counter;
  text-decoration: none;
  word-break: break-all;
}
.panel .panel-body .note-item .item-time {
  color: #ccc;
  font-size: 14px;
  text-align: right;
}
.panel .list-item {
  position: relative;
  margin: 0 0.5rem;
}
.panel .list-item .name {
  display: inline-block;
  height: 2.5rem;
  overflow: hidden;
  flex: 1;
}
.panel .list-item .time {
  color: #b3b1b1;
  font-size: 0.8rem;
  height: 2.5rem;
  word-break: keep-all;
  word-wrap: normal;
}
.panel .list-item .item-title {
  display: flex;
  padding: 0 0.5rem;
  line-height: 2.5rem;
}
.panel .list-item .item-meta {
  padding: 0 0.5rem 0.5rem;
  color: #767676;
  font-size: 0.8rem;
}
.panel .list-item:hover {
  box-shadow: rgba(51, 51, 51, 0.7) 0 0 0.8rem;
}
.panel .list-item:not(:last-of-type) {
  border-bottom: 1px solid #ccc;
}

.btn-show {
  background: transparent;
  display: inline-block;
  vertical-align: middle;
  transform: translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  backface-visibility: hidden;
  position: relative;
  transition-property: color;
  transition-duration: 0.3s;
}
.btn-show:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #A10000;
  transform: scaleX(0);
  transform-origin: 50%;
  transition-property: transform;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}
.btn-show:hover, .btn-show:focus, .btn-show:active {
  color: white;
}
.btn-show:hover:before, .btn-show:focus:before, .btn-show:active:before {
  transform: scaleX(1);
}

.contact-box {
  padding: 20px;
  margin-bottom: 50px;
}
.contact-box .heading p {
  color: rgb(179, 177, 177);
  font-size: 15px;
  margin: 1.2% 0px;
  font-family: "Play-Regular";
  line-height: 1.6em;
}
.contact-box input[type=text],
.contact-box input[type=email],
.contact-box textarea {
  width: 100%;
  padding: 12px 12px;
  border: 1px solid #BDBCBC;
  font-size: 15px;
  margin-bottom: 1.5em;
  color: #808080;
  outline: none;
  font-weight: 400;
  font-family: "Play-Regular";
}
.contact-box textarea {
  resize: none;
  height: 192px;
}
.contact-box button {
  width: 100px;
  border: 1px solid #A10000;
  border-radius: 0;
}

.about-box,
.contact-box {
  background-color: #fff;
  padding: 20px;
}
.about-box .heading h3,
.about-box .heading h1,
.about-box .heading h2,
.contact-box .heading h3,
.contact-box .heading h1,
.contact-box .heading h2 {
  color: #333;
  font-size: 2.1em;
  font-weight: 500;
  margin: 0;
  text-transform: uppercase;
}

.about-header {
  margin-top: 50px;
  margin-bottom: 30px;
}

.person-box {
  background-color: #fff;
  padding: 20px 10px;
  color: #555;
}
.person-box .avatar {
  text-align: center;
}
.person-box .avatar img {
  padding: 2px;
  max-width: 120px;
  height: auto;
  border: 1px solid #eee;
}
.person-box .name {
  text-align: center;
  color: #222;
  line-height: 40px;
  font-weight: 600;
}
.person-box .desc {
  line-height: 30px;
  text-align: center;
  border-top: 1px dotted #ccc;
  border-bottom: 1px dotted #ccc;
}
.person-box .links {
  margin-top: 10px;
  line-height: 30px;
}
.person-box .links a {
  color: #333;
  display: inline-block;
  width: 45%;
  padding: 0 10px;
}
.person-box .links a:hover {
  border-radius: 4px;
  background: #eee;
}

.about-box .about-top {
  text-align: center;
}
.about-box .about-bottom h4 {
  color: rgb(34, 34, 34);
  font-size: 1.2em;
  margin: 2.1% 0px 0px;
  line-height: 1.3em;
  font-family: "Play-Regular";
}
.about-box .about-bottom p {
  color: #b3b1b1;
  font-size: 15px;
  margin: 5px 0px 0px;
  font-family: "Play-Regular";
  line-height: 1.6em;
}
.about-box .autograph {
  line-height: 30px;
  color: #333;
  text-align: right;
}

.friend-box {
  margin: 50px 0;
  background-color: #fff;
  min-height: 300px;
  line-height: 40px;
  padding: 20px 10px;
}
.friend-box .friend-link {
  padding-left: 150px;
}
.friend-box .friend-link > div:first-child {
  width: 140px;
  border-right: 5px solid #A10000;
  font-size: 20px;
}

.link-box {
  margin: 50px 0;
  min-height: 300px;
}
.link-box .panel-body {
  padding: 10px;
  line-height: 30px;
}

.friend-apply {
  text-align: right;
  margin-bottom: 30px;
}
.friend-apply .btn {
  border: 1px solid #A10000;
  background-color: #fff;
}

.apply-dialog input {
  height: 30px;
  width: 300px;
}

.page-not-found {
  margin-top: 10vh;
}
.page-not-found p {
  text-align: center;
  font-size: 20px;
}
.page-not-found .content {
  text-align: center;
}
.page-not-found .content img {
  max-width: 100%;
}

.dialog-cookie-tip {
  display: none;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  box-shadow: rgba(51, 51, 51, 0.7) 0 0 10px;
  padding: 40px 20px;
}
.dialog-cookie-tip .dialog-footer {
  text-align: right;
}
.dialog-cookie-tip .dialog-footer .btn {
  background-color: #A10000;
  color: #fff;
}
.dialog-cookie-tip a {
  color: #CC2525;
}

.dialog-search {
  display: none;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 99;
  background-color: rgba(233, 116, 116, 0.7);
}
.dialog-search .dialog-close {
  display: inline-block;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: inherit;
  text-rendering: auto;
  font-style: normal;
  font-size: 48px;
  position: absolute;
  right: 0;
  background-color: yellow;
  padding: 5px 10px;
}
.dialog-search .dialog-close::before {
  content: "\f00d";
}
.dialog-search .dialog-body {
  padding: 80px 20px;
}
.dialog-search form {
  position: relative;
}
.dialog-search form .input-search,
.dialog-search form .input-clear {
  position: absolute;
  top: 0;
  width: 50px;
  line-height: 48px;
  background: 0 0;
  border: none;
  color: #ddd;
  outline: 0;
  padding: 0 10px;
}
.dialog-search form .input-search {
  left: 0;
  display: inline-block;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: inherit;
  text-rendering: auto;
  font-style: normal;
  font-size: 25px;
}
.dialog-search form .input-search::before {
  content: "\f002";
}
.dialog-search form .input-clear {
  right: 0;
  display: inline-block;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: inherit;
  text-rendering: auto;
  font-style: normal;
  font-size: 25px;
  display: none;
}
.dialog-search form .input-clear::before {
  content: "\f00d";
}
.dialog-search form input {
  width: 100%;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  color: #999;
  border: none;
  outline: 0;
  padding: 0 0 0 45px;
  border: 1px solid #e6e6e6;
}
.dialog-search .search-suggestion {
  width: 100%;
  border-radius: 0 0 10px 10px;
  background: #fff;
  box-shadow: 0 4px 5px 0 rgba(204, 204, 204, 0.5803921569);
  overflow: hidden;
}
.dialog-search .search-suggestion li {
  border-top: 1px solid #f2f2f2;
  line-height: 50px;
  font-size: 14px;
  padding: 0 10px;
  color: #777;
  cursor: pointer;
  list-style: none;
}
.dialog-search .search-suggestion li span {
  display: inline-block;
  width: 30px;
  height: 30px;
  font-size: 12px;
  line-height: 30px;
  text-align: center;
  background: #e5e5e5;
  margin-right: 10px;
  border-radius: 50%;
  color: #999;
}
.dialog-search .search-suggestion li.active {
  background-color: rgba(153, 153, 153, 0.1);
}
.dialog-search.inputting form .input-clear {
  display: block;
}
.dialog-search.inputting form input {
  padding: 0 45px;
}

.close-page {
  background-color: #3498db;
}
.close-page .box {
  width: 648px;
  margin: 10vh auto;
  background-color: #fff;
  padding: 50px;
  border-radius: 5px;
  box-shadow: rgba(51, 51, 51, 0.7) 0 0 10px;
}
.close-page .box h1 {
  text-align: center;
  margin-bottom: 20px;
}
.close-page .box .content {
  color: #555;
}

.agreement-box {
  margin-top: 30px;
  margin-bottom: 30px;
}
.agreement-box a {
  color: #1570a6;
}
.agreement-box .update-date .date-value {
  font-weight: bold;
}
.agreement-box .print-bar {
  margin-top: 20px;
  background-color: #ececec;
  text-align: right;
}
.agreement-box .print-bar .print-btn {
  cursor: pointer;
}
.agreement-box .title {
  color: #b63b00;
  font-size: 2em;
  display: inline-block;
  vertical-align: top;
  margin: 0.7em 0.3em 0.7em -0.02em;
}
.agreement-box .nav-list {
  margin-top: 0.3em;
}
.agreement-box .nav-list li {
  background-color: #a10000;
  cursor: pointer;
  font-size: 0.9em;
  color: #fff;
  width: 90%;
  padding: 5%;
  margin-bottom: 5px;
  list-style: none;
}
.agreement-box .nav-list a {
  color: #fff;
}
.agreement-box .nav-panel {
  margin-top: 0.3em;
  padding-bottom: 0.9rem;
  margin-bottom: 0.7rem;
}
.agreement-box .nav-panel .nav-header {
  color: #a10000;
  font-size: 1.6em;
  border: 1px solid #a10000;
  border-left: 2em solid #a10000;
  padding: 0.2em;
}
.agreement-box .nav-panel .nav-desc {
  padding-top: 0.5em;
  line-height: 1.5em;
}
.agreement-box .nav-panel .nav-desc .b {
  font-weight: bold;
}
.agreement-box .nav-panel .nav-desc ul {
  margin-left: 3em;
}

@media (max-width: 640px) {
  .metro-grid a {
    width: 120px;
    height: 40px;
    line-height: 40px;
    font-size: 20px;
  }
  .metro-grid a.unknow {
    display: none;
  }
  .nav-bar .nav-bar-toggle {
    padding: 15px 20px;
    display: inline-block;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    font-size: inherit;
    text-rendering: auto;
    font-style: normal;
    font-size: 30px;
  }
  .nav-bar .nav-bar-toggle::before {
    content: "\f0c9";
  }
  .nav-bar ul {
    display: none;
    background-color: #CC2525;
  }
  .nav-bar ul li {
    display: block;
    line-height: 40px;
    height: auto;
  }
  .nav-bar ul .sub-nav {
    display: block;
    position: static;
    margin: 0;
    margin: 0 -20px;
    width: auto;
  }
  .nav-bar ul .sub-nav a {
    color: #fff;
  }
  .nav-bar ul.nav-right {
    float: none;
  }
  .nav-bar .new-tip {
    display: none;
  }
  .nav-bar.open ul {
    display: block;
  }
  .nav-bar.open .nav-bar-toggle::before {
    content: "\f00d";
  }
}